          }
        } else {
          _this4.$elementMessage(r.Message, "error");
        }
      });
    },
    submitDlocal: function submitDlocal(params) {
      var _this5 = this;

      this.dialogDlocal.loading = true;
      this.newWin = window.open((this.$root.$children[0].baseUrl == "/my" ? "/my" : "") + "/blank.html", "pay");
      var params2 = {
        code: this.coupon,
        methodId: params.methodId,
        platformType: 10,
        domain: window.location.origin + '/',
        flow: 'REDIRECT',
        country: params.country,
        name: params.name,
        city: params.city,
        street: params.street,
        number: params.number,
        document: params.document,
        phone: params.phone,
        email: params.email
      };
      var url = "api.ShopifyOrder.createPay";

      if (this.openType == "repay") {
        //重新支付
        url = "api.ShopifyOrder.reCreatePay";
      }

      params2.id = this.payparams.id;
      this.$apiCall(url, params2, function (r) {
        _this5.dialogDlocal.loading = false;
