        if (r.ErrorCode == "9999" && r.Data.Results.url) {
          _this5.newWin.location = r.Data.Results.url;
          _this5.paystatus = 1;
        } else {
          _this5.$elementMessage(r.ErrorCode == "9999" ? "fail" : r.Message, "error");

          _this5.newWin.close();
        }
      });
    },
    orderPay: function orderPay(openType) {
      var _this6 = this;

      console.log(openType);
      this.openType = openType;

      if (!this.platformType) {
        this.$elementMessage("Please select payment method", "error");
        return;
      } //支付


      this.$Burying({
        object: "9003",
        objectId: this.platformType
      });
      var params = {
        code: this.coupon,
        platformType: this.platformType,
        success_url: window.location.origin + '/orderPay?paystatus=2',
        cancel_url: window.location.origin + '/orderPay?paystatus=3'
      };
      var type = "";
      var paymentId = "";

      if (this.platformType && !Number(this.platformType) && this.platformType.indexOf('underline') != -1) {
        //线下支付
        paymentId = this.platformType.substring(9);
        type = 12;
      } else {
        type = Number(this.platformType);
      }

      switch (type) {
        //underline
        case 12:
          this.dialogUnderline.isShow = true;
          this.dialogUnderline.paymentId = paymentId;
          var item = this.payTypes.filter(function (item) {
            return item.id == paymentId;
          });
          this.dialogUnderline.params = item.length && item[0].params ? JSON.parse(item[0].params) : "";
          this.dialogUnderline.payAmount = this.totalAllGoodsAndFreight;
          break;
        //dlocal

        case 10:
          this.dialogDlocal.isShow = true;
          this.dialogDlocal.invoiceId = this.payparams.id;
          break;
        //CheckOut

        case 4:
          this.dialogCheckOut.isShow = true;
          break;
        //paypal

        case 3:
          console.log(this.$root.$children[0].baseUrl);
          this.newWin = window.open((this.$root.$children[0].baseUrl == "/my" ? "/my" : "") + "/blank.html", "pay");
          break;
        //ebanx

        case 2:
          this.newWin = window.open((this.$root.$children[0].baseUrl == "/my" ? "/my" : "") + "/blank.html", "pay");
          break;
        //stripe

        case 1:
          break;

        default:
          //bonus
          break;
      }

      if (type == 4 || type == 10 || type == 12) {
        //CheckOut或者dlocal或者underline
        return;
      }

      this.loading = true;
      var url = "api.ShopifyOrder.createPay";

      if (openType == "repay") {
        //重新支付
        url = "api.ShopifyOrder.reCreatePay";
      }

      params.id = this.payparams.id;
      this.$apiCall(url, params, function (r) {
        _this6.loading = false;

        if (r.ErrorCode == "9999") {
          if (openType == "repay") {
            //重新支付
            localStorage.setItem("c_returnPayId", params.id);
          } else {
            localStorage.setItem("c_returnPayId", r.Data.Results.id);
          }

          localStorage.setItem("c_returnPaySessionId", r.Data.Results.sessionId); //bonus

          if (_this6.platformType == 6) {
            _this6.paystatus = 2;

            if (openType == "repay") {
              //重新支付
              _this6.paySuccess(params.id, r.Data.Results.sessionId);
            } else {
              _this6.paySuccess(r.Data.Results.id, r.Data.Results.sessionId);
            }
          } else {
            //stripe
            if (_this6.platformType == 1) {
              var data = r.Data.Results;
              var stripe = Stripe(data.pk);
              stripe.redirectToCheckout({
                sessionId: data.sessionId
              });
            } else {
              _this6.newWin.location = r.Data.Results.url;
            }

            _this6.paystatus = 1;
          }
        } else {
          _this6.$elementMessage(r.Message, "error");

          _this6.closePayPage();
        }
      });
    },
    paySuccess: function paySuccess(id, sessionId) {
      var _this7 = this;

      localStorage.removeItem("c_returnPayId");
      localStorage.removeItem("c_returnPaySessionId");
      this.$apiCall("api.ShopifyOrder.returnPay", {
        id: id,
        sessionId: sessionId
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this7.$elementMessage("Successfully", "success");
        } else {
          _this7.$elementMessage(r.Message, "error");
        }
      });
    },
    closePayPage: function closePayPage() {
      //关闭支付新开页面
      if (this.newWin) {
        this.newWin.close();
      } // clearInterval(this.payTime);


      localStorage.removeItem("c_returnPayStatus");
    },
    changeBonusPlatform: function changeBonusPlatform() {
      if (this.bonus == 0 || this.bonus < this.totalAllGoodsAndFreight) {
        return;
      } else {
        if (this.platformType == 6) {
          this.platformType = "";
          return;
        }

        this.platformType = 6;
      }
    },
    changePlatform: function changePlatform(type) {
      if (this.bonusStatus != 1) {
        this.platformType = type;
      } else {
        if (this.platformType != 6) {
          this.platformType = type;
        }
      }
    },
    getBonus: function getBonus() {
      var _this8 = this;

      this.$apiCall("api.Invoice.getBonus", {}, function (r) {
        if (r.ErrorCode == "9999") {
          _this8.bonus = r.Data.Results.bonus ? parseFloat(Number(r.Data.Results.bonus).toFixed(2)) : 0;

          if (_this8.bonus > _this8.totalAllGoodsAndFreight) {
            _this8.platformType = 6;
          } else {
            var accountPayment = localStorage.getItem('accountPayment') ? JSON.parse(localStorage.getItem('accountPayment')) : "";

            if (_this8.payTypes.length) {
              if (accountPayment.platformType == 12) {
                _this8.platformType = accountPayment.extJson && accountPayment.extJson.paymentId ? "underline".concat(accountPayment.extJson.paymentId) : _this8.payTypes[0].accountType;
              } else if (accountPayment.platformType == 6) {} else {
                _this8.platformType = accountPayment.platformType;
              }
            }
          }

          _this8.bonusStatus = r.Data.Results.bonusStatus;
        } else {}
      });
      this.$apiCall("api.AccountPayment.getPaymentAccounts", {}, function (r) {
        if (r.ErrorCode == "9999") {
          _this8.payTypes = [];
          if (!r.Data.Results || !r.Data.Results.length) return;
          r.Data.Results.forEach(function (item) {
            item.icon = require('@/assets/pay/' + Number(item.accountType) + '.png');

            if (item.accountType == 12) {
              _this8.$set(item, "accountType", "underline".concat(item.id));

              _this8.$set(item, "name", "".concat(item.params ? JSON.parse(item.params).cname : ''));
            }

            if (item.status == 1) {
              _this8.payTypes.push(item);
            }
          });
        } else {}
      });
    },
    getItems: function getItems() {
      var _this9 = this;

      var items = localStorage.getItem('needPayOrders') ? JSON.parse(localStorage.getItem('needPayOrders')) : "";
      if (!items) return;
      this.openType = items.type;
      this.items = items.items || [];
      this.payparams.id = items.invoice.id;
      this.items.forEach(function (item) {
        _this9.$set(item, "totalAllAmount", Number(item.itemAllAmount) + Number(item.shippingAllAmount));

        _this9.$set(item, "shippingJsonInfo", item.shippingJson ? JSON.parse(item.shippingJson) : "");

        _this9.$set(item, "shippingAllAmount", item.shippingAllAmount ? Number(item.shippingAllAmount) : "");
      });
      this.$set(this, "totalAllGoodsAmount", this.countTotal(this.items, 'itemAllAmount')); //商品总价

      this.$set(this, "totalAllFreight", this.countTotal(this.items, 'shippingAllAmount')); //运费汇总

      this.$set(this, "totalAllGoodsAndFreight", this.countTotal(this.items, 'totalAllAmount')); //商品总价+运费汇总
      //重新支付时获取之前支付的时候有没有占用优惠券

      if (this.openType == 'repay') {
        this.$showLoading();
        this.$apiCall("api.Coupon.findHistory", {
          relateId: this.payparams.id,
          status: 2
        }, function (r) {
          _this9.$hideLoading();

          if (r.ErrorCode == "9999") {
            if (!r.Data.Results || !r.Data.Results.length) return;

            if (r.Data.Results[0].extra) {
              _this9.coupon = JSON.parse(r.Data.Results[0].extra).code;

              _this9.getCoupon();
            }
          } else {
            _this9.$elementMessage(r.Message, "error");
          }
        });
      }
    },
    countTotal: function countTotal(arr, keyName) {
      var $total = 0;
      $total = arr.reduce(function (total, currentValue, currentIndex, arr) {
        return currentValue[keyName] ? total + currentValue[keyName] : total;
      }, 0);
      return parseFloat(Number($total).toFixed(2));
    }
  }
};